	data() {
		return {
			array: [
				{
					id: 0,
					name: '园区加载中，请稍等。。',
					logo: 'http://obs-312.obs.cn-east-2.myhuaweicloud.com/20190426206c5f6d08.jpg',
					location: '330108',
					address: '聚业路28号',
					sort: 0,
					createBy: '黄鹏飞2号',
					createDate: '2019-04-19 21:04:03',
					updateBy: '黄鹏飞2号',
					updateDate: '2019-04-26 20:37:44',
					status: 1
				}
			],
			index: 0,
			phoneNumber: '',
			passwd: '',
			isShowOauth: false
		};
	},
	onShow() {},
	onLoad() {
		const that = this;
		uni.request({
			url: uni.getStorageSync('tempUrl') + 'common/locationByIp',
			method: 'GET',
			success: res => {
				console.log(res);
				const data = res.data.result;
				console.log('当前位置的区域code：' + data.ad_info.adcode);
				uni.request({
					url: uni.getStorageSync('tempUrl') + 'park/selectListByLocation',
					// data: { location: data.ad_info.adcode, pageNum: '1', pageSize: '99' },
					data: { location: '330108', pageNum: '1', pageSize: '99' },
					method: 'GET',
					success: res => {
						const data = res.data.content;
						console.log(data);
						that.array = data;
					}
				});
			}
		});
	},
	methods: {
		doLogin() {
			uni.hideKeyboard();
			uni.showLoading({
				title: '提交中...'
			});
			setTimeout(() => {
				if (this.array[this.index].id === 0) {
					uni.hideLoading();
					uni.showToast({ title: '您还未选择园区', icon: 'none' });
				} else {
					uni.request({
						url: uni.getStorageSync('tempUrl') + 'app/selectListByParkId',
						data: { parkId: this.array[this.index].id, pageNum: '1', pageSize: '99' },
						method: 'GET',
						success: res => {
							const data = res.data.content;
							console.log(data);
							uni.hideLoading();
							uni.setStorageSync('parkId', this.array[this.index].id);
							uni.setStorageSync('parkName', this.array[this.index].name);
							uni.showToast({ title: '登录成功', icon: 'success' });
							uni.reLaunch({
								url: '../tabBar/home'
							});
						}
					});
				}
			}, 1000);
		}
